import Axios from 'axios';

const save = (formData) => {
    "use strict";
    if (formData.status) {
        formData.status = 1;
    } else {
        formData.status = 0;
    }

    if (formData.feature) {
        formData.feature = 1;
    } else {
        formData.feature = 0;
    }

    return Axios
        .post(`${window.adminUrl}/post`, formData,
            {
                withCredentials: true
            }
        )
        .then((res) => {
            return res;
        });
};

const findById = (id) => {
    "use strict";
    return Axios
        .get(`${window.adminUrl}/post/${id}`,
            {
                withCredentials: true
            }
        )
        .then((res) => {
            if (res.status == 200) {
                return res.data;
            }
        });
};

export  default  {
    save,
    findById
}


