<template>
    <div class="form-elements">
        <div class="row">
            <div class="col-sm-12">
                <vuestic-widget class=" color-buttons" headerText="">
                    <div class="row">
                        <div class=".col-md-1">
                            <button class="btn btn-secondary focus btn-micro">
                                <div class="btn-with-icon-content"><i class="glyphicon glyphicon-floppy-disk"></i> Save & List</div>
                            </button>
                        </div>
                        &nbsp; &nbsp;
                        <div class=".col-md-1">
                            <button class="btn btn-secondary focus btn-micro" @click="save">
                                <div class="btn-with-icon-content"><i class="glyphicon glyphicon-floppy-disk"></i> Save</div>
                            </button>
                        </div>
                    </div>
                </vuestic-widget>
            </div>
        </div>
        <div class="row">
            <div class="col-md-8">
                <vuestic-widget headerText="Create Post">
                    <form>
                        <div class="row">
                            <div class="col-md-12">
                                <fieldset>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required v-model="formData.title"/>
                                            <label class="control-label" for="simple-input">Title</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required v-model="formData.slug"/>
                                            <label class="control-label" for="simple-input">Slug</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <textarea type="text" id="simple-textarea" required v-model="formData.teaser"></textarea>
                                            <label class="control-label" for="simple-textarea">Introtext</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <froala :tag="'textarea'" v-model="formData.content"></froala>
                                            <label class="control-label" for="simple-textarea">Body</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group form-group-w-btn">
                                        <div class="btn btn-micro btn-primary">UPLOAD</div>
                                    </div>
                                </fieldset>
                            </div>
                        </div>
                    </form>
                </vuestic-widget>
            </div>
            <div class="col-md-4">
                <vuestic-widget headerText="Extra">
                    <form>
                        <div class="row">
                            <div class="col-md-12">
                                <fieldset>
                                    <vuestic-multi-select
                                            label="Category"
                                            v-model="formData.category"
                                            v-bind:options="categoryOptions">
                                    </vuestic-multi-select>
                                    <vuestic-switch v-model="formData.feature">
                                        <span slot="trueTitle">Featured</span>
                                        <span slot="falseTitle">Not Feature</span>
                                    </vuestic-switch>
                                    <br/>
                                    <vuestic-switch v-model="formData.status">
                                        <span slot="trueTitle">Publish</span>
                                        <span slot="falseTitle">Unpublish</span>
                                    </vuestic-switch>
                                </fieldset>
                            </div>
                        </div>
                    </form>
                </vuestic-widget>
                <vuestic-widget headerText="Meta">
                    <form>
                        <div class="row">
                            <div class="col-md-12">
                                <fieldset>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required/>
                                            <label class="control-label" for="simple-input">Meta Title</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <input id="simple-input" required/>
                                            <label class="control-label" for="simple-input">Meta Description</label><i class="bar"></i>
                                        </div>
                                    </div>
                                    <div class="form-group">
                                        <div class="input-group">
                                            <textarea type="text" id="simple-textarea" required></textarea>
                                            <label class="control-label" for="simple-textarea">Meta Keyword</label><i class="bar"></i>
                                        </div>
                                    </div>
                                </fieldset>
                            </div>
                        </div>
                    </form>
                </vuestic-widget>
            </div>
        </div>
    </div>
</template>

<script>
    import VueFroala from 'vue-froala-wysiwyg';
    import VuesticSwitch from 'components/vuestic-components/vuestic-switch/VuesticSwitch'
    import VuesticSimpleSelect from 'components/vuestic-components/vuestic-simple-select/VuesticSimpleSelect'
    import VuesticMultiSelect from 'components/vuestic-components/vuestic-multi-select/VuesticMultiSelect'

    import PostService from 'services/post';
    import CategoryService from 'services/category';

    export default {
        name: 'form-elements',
        components: {
            VueFroala,
            VuesticSwitch,
            VuesticSimpleSelect,
            VuesticMultiSelect
        },
        computed: {},
        data () {
            return {
                categoryOptions: [],
                formData: {
                    title: '',
                    slug: '',
                    teaser: '',
                    category: [],
                    content: '',
                    status: true,
                    feature: false
                }
            }
        },
        methods: {
            save(){
                PostService
                    .save(this.formData)
                    .then((res) => {
                        console.log(res);
                    })

            }
        },
        mounted () {
            let id = this.$route.params.id;
            if (id) {
                PostService
                    .findById(id)
                    .then((res) => {
                            this.formData.title = res.title;
                            this.formData.slug = res.slug;
                            this.formData.teaser = res.teaser;
                            this.formData.content = res.content;
                            this.formData.category = res.category;
                            this.formData.status = res.status ? true : false;
                            this.formData.feature = res.status ? true : false;
                        }
                    )
            }
            CategoryService
                .getCategory()
                .then(res => {
                    this.categoryOptions = res;
                });

            this.$validator.validateAll();
        }
    }
</script>

<style lang="scss">
    .abc-checkbox, .abc-radio {
        display: flex !important;
        justify-content: flex-start;
    }

    input[type=checkbox]:disabled + label, input[type=radio]:disabled + label,
    input[type=checkbox]:disabled, input[type=radio]:disabled {
        cursor: not-allowed;
    }

    .non-padding-left {
        padding-left: 0px;
    }

    .fr-box.fr-basic.fr-top {
        margin-top: 20px;
    }

    .vuestic-switch .vuestic-switch-option.false-option.active {
        background: #ff0000;
        border-bottom: 0.125rem solid #ff0000;
    }
</style>


